import React, {Component} from 'react'
import { Animation } from 'styled-animation'
import { SectionsContainer, Section } from 'react-fullpage'

class Page1 extends Component {
  constructor() {
    super()
    this.state = {
      line1: '106%',
      line2: '106%',
    }
  }
  render() {
    return (
      <div style={{height: '85%'}}>
        <img style={{width: '95%', height: '60%', marginTop: '12%'}} src="/image/mobile/mobile_1.gif" />
        <br />
        <div style={{color: '#666666',
                  fontFamily: "Helvetica",
                  fontSize: '10pt',
                  textAlign: 'center',
                  position: 'relative',
                }}>
          <div style={{marginLeft: '25%', marginRight: '25%'}}>
            <img src="/image/title_SB_cn.png"
              style={{opacity: 0.9, width: '100%', height: '8%'}}
            />
            <div style={{
              backgroundColor: '#FFE300',
              height: '10px',
              width: this.state.line1,
              transition: "all 0.5s linear",
              transitionDelay: '5s',
              marginTop: '-15px',
              marginLeft: '-3%'
            }} />
          </div>
          <br />
          <div style={{marginLeft: '20%', marginRight: '20%'}}>
            <img src="/image/title_SB_en.png"
              style={{opacity: 0.9, width: '100%', height: '8%'}}
            />
            <div style={{
              backgroundColor: '#FFE300',
              height: '10px',
              width: this.state.line2,
              transition: "all 0.5s linear",
              transitionDelay: '5s',
              marginTop: '-15px',
              marginLeft: '-3%'
            }} />
          </div>
          <br />
          <br />
          
          <br />
          <br />
          
          <br />
          <br />
          
          <br />
          
        </div>
      </div>
    )
  }
}

