class Page4 extends Component {
  constructor() {
    super()
    this.state = {
      line1: '106%',
      line2: '106%',
    }
  }

  render() {
    return (
      <div className="right" style={{height: '85%'}}>
        <img src="/image/mobile/mobile_4.gif" style={{width: '90%', height: '48%', marginTop: '14%', marginRight: '5%'}}/>
        <br />
        <div style={{color: '#666666',
                  fontFamily: "Helvetica",
                  fontSize: '10pt',
                  textAlign: 'center',
                  position: 'relative',
                }}>
          <div style={{marginLeft: '25%', marginRight: '25%'}}>
            <img src="/image/title_SM_cn.png"
              style={{opacity: 0.9, width: '100%', height: '6%'}}
            />
            <div style={{
              backgroundColor: '#FFE300',
              height: '10px',
              width: this.state.line1,
              transition: "all 0.5s linear",
              transitionDelay: '2s',
              marginTop: '-15px',
              marginLeft: '-3%'
            }} />
          </div>
          <br />
          <div style={{marginLeft: '20%', marginRight: '20%'}}>
            <img src="/image/title_SM_en.png"
              style={{opacity: 0.9, width: '100%', height: '6%'}}
            />
            <div style={{
              backgroundColor: '#FFE300',
              height: '10px',
              width: this.state.line2,
              transition: "all 0.5s linear",
              transitionDelay: '2s',
              marginTop: '-15px',
              marginLeft: '-3%'
            }} />
          </div>
          <br />
          
          <br />
          
          <br />
          <br />
          
          <br />
          <br />
          
          <br />
          
          <br />
          
          <br />
          <br />
          
        </div>
      </div>
    )
  }
}

export default class extends Component {
  constructor() {
    super()
  }

  render() {
    let options = {
      activeClass:          'active',
      anchors:              ['sectionOne', 'sectionTwo', 'sectionThree', 'sectionFour'],
      arrowNavigation:      true,
      className:            'SectionContainer',
      delay:                2000,
      navigation:           false,
      scrollBar:            false,
      sectionClassName:     'Section',
      sectionPaddingTop:    '0',
      sectionPaddingBottom: '0',
      verticalAlign:        false
    }
    return (
      <Animation name="slideInUp" duration="2s" >
        <SectionsContainer {...options} >
          <Section><Page1 /></Section>
          <Section><Page2 /></Section>
          <Section><Page3 /></Section>
          <Section><Page4 /></Section>
        </SectionsContainer>
      </Animation>
    )
  }
}



