import React, {Component} from 'react'
import {Animation} from 'styled-animation'
import { SectionsContainer, Section } from 'react-fullpage'

class Footer extends Component {
  constructor() {
    super()
  }
  render() {
    let { isAddress } = this.props
    if(isAddress) {
      return (
        <div style={{textAlign: 'center', fontFamily: 'Helvetica', fontSize: 14}}>
          media@xiaodeqihua.com<br /><br />
            696<br /><br />
          No.696 Weihai Road, Jing An District ,Shanghai
        </div>
      )
    }
    else {
      return (
        <div style={{textAlign: 'center', fontFamily: 'Helvetica', fontSize: 14}}>
          ?!<br />
          GET A PROJECT? Wed love to hear about it. Get in touch.
        </div>
      )
    }
  }
}

class Page1 extends Component {
  constructor() {
    super()
  }
  render() {
    return (
      <div style={{marginTop: '150px'}}>
        <div style={{textAlign: 'center', fontFamily: 'Helvetica', height: 20, fontSize: 30, marginTop: '18%'}}>
          <div>,</div>
        </div>
        <div style={{
          background: "#FFE300",
          height: 15,
          marginBottom: '20%',
          width: 400,
          marginLeft: 'auto',
          marginRight: 'auto',
          marginBottom: '20%'}} />
        <Footer isAddress={true} />
      </div>
    )
  }
}

class Page2 extends Component {
  constructor() {
    super()
  }
  render() {
    return (
      <div style={{marginTop: '150px', height: '55%', width: '100%', paddingTop: '15%'}}>
        <div style={{
          marginLeft: '10%',
          fontSize: 50,
          fontFamily: 'Helvetica',
          width: 'fit-content', width: '-webkit-fit-content'
        }}>
          <div style={{width: 'fit-content', width: '-webkit-fit-content', height: 30}}>This is what we do</div>
          <div style={{width: '100%', height: 15, background: '#FFE300'}}></div>
        </div>
        <div style={{
          marginLeft: '10%',
          marginTop: '3%',
          fontSize: 14,
          fontFamily: 'Helvetica'
        }}>
          <br />
          :<br />
          <br />
        </div>
        <div style={{
          marginLeft: '10%',
          marginTop: '3%',
          fontSize: 14,
          fontFamily: 'Helvetica'
        }}>
          <br />
          :<br />
          <br />
        </div>
      </div>
    )
  }
}

export default class extends Component {
  constructor() {
    super()
  }
  mapAnchors() {
    let r = []
    for(let i = 1; i <= 2; ++i) {
      r = r.concat(['section' + i])
    }
    return r
  }
  render() {
    let options = {
      activeClass:          'active',
      anchors:              this.mapAnchors(),
      arrowNavigation:      true,
      className:            'SectionContainer',
      delay:                2000,
      navigation:           false,
      scrollBar:            false,
      sectionClassName:     'Section',
      sectionPaddingTop:    '0',
      sectionPaddingBottom: '0',
      verticalAlign:        false
    }
    return (
      <Animation name="slideInUp" duration="2s">
        <SectionsContainer {...options} >
          <Section><Page1 /></Section>
          <Section><Page2 /></Section>
        </SectionsContainer>
      </Animation>
    )
  }
}



