import React, {Component} from 'react'
import { Animation } from 'styled-animation'
import { SectionsContainer, Section } from 'react-fullpage'
import './css/homep4.css'
import './css/homep1.css'
import './css/homep2.css'
import './css/homep3.css'

class Page1 extends Component {
  constructor() {
    super()
    this.state = {
      line1: 0,
      line2: 0,
      running: 'paused'
    }
  }

  render() {
    return (
      <div style={{height: '85%'}}>
        <div className='gifp1'
          onMouseMove={() => this.setState({running: 'running'})}
          onMouseOut={() => this.setState({running: 'paused'})}
          style={{animationPlayState: this.state.running}} />
        <br />
        <div style={{color: '#666666',
                  fontFamily: "Helvetica",
                  fontSize: '13pt',
                  textAlign: 'right',
                  position: 'relative',
                  right: '50px',
                  width: '40%',
                  marginLeft: '60%'
                }}>
          <br />
          
          <br />
          <br />
          
          <br />
          <br />
          
          <br />
          
          <br />
          <br />
          <div style={{float: 'right', width: 345}}>
            <img src="/image/title_SB_cn.png"
              onMouseMove={() => {this.setState({ line1: 345, line2: 425})}}
              onMouseOut={() => {this.setState({ line1: 0, line2: 0})}}
              style={{opacity: 0.9}}
            />
            <div style={{
              backgroundColor: '#FFE300',
              height: '15px',
              width: this.state.line1 + 'px',
              transition: "all 0.5s linear",
              marginTop: '-15px'
            }} />
          </div>
          <br />
          <div style={{float: 'right', width: 425, marginTop: 8}}>
            <img src="/image/title_SB_en.png"
              onMouseMove={() => {this.setState({ line1: 345, line2: 425})}}
              onMouseOut={() => {this.setState({ line1: 0, line2: 0})}}
              style={{opacity: 0.9}}
            />
            <div style={{
              backgroundColor: '#FFE300',
              height: '15px',
              width: this.state.line2 + 'px',
              transition: "all 0.5s linear",
              marginTop: '-25px'
            }} />
          </div>
        </div>
      </div>
    )
  }
}

